    "id":2,
    "title":"1909-Empire Strikes Back",
    "content":`The 31 March Incident (Turkish: 31 Mart Vakası, 31 Mart Olayı, 31 Mart Hadisesi, or 31 Mart İsyanı) was a political crisis within the Ottoman Empire in 1909,
     during the Second Constitutional Era. Occurring soon after the 1908 Young Turk Revolution, 
     in which the Committee of Union and Progress (CUP) had successfully restored the liberal 1876 constitution and ended the absolute rule of Sultan Abdul Hamid II,
     it is sometimes referred to as a countercoup or counterrevolution. It consisted of a general uprising against the CUP within Istanbul, 
     largely led by reactionary groups, particularly Islamists opposed to the secularising influence of the CUP and supporters of absolutism,
     although liberal opponents of the CUP within the Ottoman Liberty Party also played a lesser role.[2] The crisis ended after eleven days, 
     when troops loyal to the CUP re-entered Istanbul. 
     In April 1909 in Constantinople, a group of soldiers began a counter-revolution (see 31 March Incident). Atatürk was instrumental in suppressing the revolt.`,
