    "id":6,
    "title":"1917-Sinai-Palestine Campaign",
    "content":`In July 1917, he was appointed to the command of the Seventh Army, 
    eplacing Fevzi Pasha on 7 August 1917, who was under the command of the German general Erich von Falkenhayn's Yildirim Army Group 
    (after the British forces of General Edmund Allenby captured Jerusalem in December 1917, Erich von Falkenhayn was replaced by Otto Liman von Sanders 
        who became the new commander of the Yıldırım Army Group in early 1918.)[56] Atatürk did not get along well with General von Falkenhayn and, 
        together with Miralay İsmet Bey, wrote a report to Grand Vizier Talaat Pasha regarding the grim situation and lack of adequate resources 
        in the Palestinian front. However, Talaat Pasha ignored their observations and refused their suggestion to form a stronger defensive line to 
        the north, in Ottoman Syria (in parts of the Beirut Vilayet, Damascus Vilayet, and Aleppo Vilayet), with Turks instead of Germans in command.[56]
         Following the rejection of his report, Atatürk resigned from the Seventh Army and returned to Constantinople.[56] There, 
         he was assigned with the task of accompanying the crown prince (and future sultan) Mehmed Vahideddin during his train trip to Austria-Hungary 
         and Germany.[56] While in Germany, Atatürk visited the German lines on the Western Front and concluded that the Central Powers would soon lose
          the war.[56] He did not hesitate to openly express this opinion to Kaiser Wilhelm II and his high-ranking generals in person.[56]
           During the return trip, he briefly stayed in Karlsbad and Vienna for medical treatment.[56]`,
