import { useState } from "react"
import "./background.css"
import FrontScreen from "./FrontScreen"
import Music from "./Music"


function App() {

const [frontScreenHandler,setFrontScreenHandler]=useState(-1)


const recieveClose=()=>{
setFrontScreenHandler(-1)
}

const getContent = (x) =>{
  // console.log(Data[x].content)
  // setSendOpen(x)
  setFrontScreenHandler(x)
}


  return (
    <>
    <div className="background">
      <div className="text text--1881" onClick={()=>getContent(0)}>1881-New Hope</div>
      <div className="text text--1909" onClick={()=>getContent(1)}>1909-Empire Strikes Back</div>
      <div className="text text--1910" onClick={()=>getContent(2)}>1910-Tripoli War</div>
      <div className="text text--1912" onClick={()=>getContent(3)}>1912-Balkan War</div>
      <div className="text text--1914" onClick={()=>getContent(4)}>1914-Gallipoli Campaign</div>
      <div className="text text--1917" onClick={()=>getContent(5)}>1917-Sinai-Palestine</div>
      <div className="text text--1919" onClick={()=>getContent(6)}>1919-First Step</div>
      <div className="text text--1920" onClick={()=>getContent(7)}>1920-First President</div>
      <div className="text text--1921" onClick={()=>getContent(8)}>1921-Supreme Commander</div>
      <div className="text text--1922" onClick={()=>getContent(9)}>1922-Cold Sea for Greeks</div>
      <div className="text__table" onClick={()=>getContent(10)}>
      <div className="text text--1923" onClick={()=>getContent(11)}>1923-A New Republic</div>
      <div className="text text--rev" onClick={()=>getContent(12)}>Revolutions</div>
      <div className="text text--1938" onClick={()=>getContent(13)}>1938-Death of Father of Turks</div>
      </div>
      {frontScreenHandler>=0&&<FrontScreen receiveClose={recieveClose} sendData={frontScreenHandler}/>}
      </div>
      <Music/>

</>
  );
}

export default App;
