import style from "./FrontScreen.module.css";
import { useState } from "react";
import { Data } from "./Data";
const FrontScreen = (props) => {
  const closeContent = () => {
    props.receiveClose();
  };

  const [photoId, setPhotoId] = useState(Data[props.sendData].image[0]);
 const [zoomExist,setZoomExist]=useState(false)

 const zoom = (x) =>{
     setZoomExist(x)
 }

  const changePhoto = (item) => {
    setPhotoId(item.target.src);
  };

  const fetchingData = Data[props.sendData].content;
  const fetchingImages = Data[props.sendData].image;


  return (
    <>
      <div className={style.TransparentBackground}></div>

      <div className={style.FrontScreen}>
        <div className={style.close} onClick={closeContent}>
          X
        </div>
        <div className={style.text}>
          <div className={style.outside}>
            <img
              className={style.image}
              src={photoId}
              width="150"
              height="150"
             onClick={()=>zoom(true)}
            />
          </div>
          {fetchingData}
        </div>

        <div className={style.bottom}>
          {fetchingImages.map((item) => (
            <img
              src={item}
              width="100"
              height="100"
              onClick={(item) => changePhoto(item)}
            />
          ))}
        </div>
    {zoomExist&&<div className={style.zoomedScreen}>
          <img src={photoId} />
          <div className={style.close} onClick={()=>zoom(false)}>
            X
          </div>
        </div> }
      </div>
    </>
  );
};

export default FrontScreen;
